import React from 'react';
import { Container, Media, Row, Col, Button } from 'reactstrap';
import './media.css';
import { BrowserRouter, Route, Link } from "react-router-dom";
import ReactDOM from "react-dom";


class Menu extends React.Component {
	constructor(props){
		super(props);
		
		this.state = {};
	}
	
	render(){
		
		const menu = this.props.songs.map((song) => {
			return(
                <Container>
                    
				<div key={song.id} id="unit" >	
                
                    <Media tag="li" >
						<Container>
							<Row>
								<Col>
								<Media left >
							<Media object src={song.image} alt={song.name} className="logo" />
								</Media>
								</Col>
								<Col>
								<Media body className="ml-3">
                            <Container className="song">
							<Media heading className="title">{song.name}</Media>
							<div ><strong>Album - </strong>{song.album}</div>
                            <div ><strong>Release - </strong>{song.release}</div>
							<div ><strong>Description - </strong>{song.description}</div>                           						                  
							</Container>
							</Media>
							</Col>
							</Row>
							</Container>
						</Media>
						
                    <Container><Row><Col><div className="d-grid gap-2 listen">
                            {/* <Route path={song.link}></Route> */}
								<Button variant="success" size="lg">
                            Listen on Spotify
                            </Button>
							
                            </div>			</Col></Row></Container>
						
					
				</div>
                </Container>
			);
		});
		
		return(
			<div className="container">
				<div className="row">
					<Media list>
						{menu}
					</Media>
				</div>
			</div>
		);
	}
}

// 'Keys' - It helps identify which items have changed, are added or removed.
// ReactDOM.render(
// 	<BrowserRouter>
// 	  <Menu />
// 	</BrowserRouter>,
// 	document.getElementById("root")
//   );
export default Menu;